















































import Vue from "vue";
import { coin } from "./coinObject";

export default Vue.extend({
  props: {
    coinName: String,
    coinSymbol: String,
    currentPrice: String,
    hourChange: String,
    dailyChange: String,
    marketDat: Array
  },

  data: () => ({
    width: 2,
    radius: 3,
    padding: 1,
    lineCap: "round",
    gradient: ["#00c6ff", "#F0F", "#FF0"],
    gradientDirection: "top",
    fill: true,
    type: "trend",
    autoLineWidth: false,
  }),

  computed: {
    getCoinsHeld() {
      let index = this.$store.state.testCoins.findIndex(
        (x: coin) => x.name === this.coinName
      );
      return this.$store.state.testCoins[index].coinsHeld;
    }
  },

  methods: {
    test() {
      console.log(Number(this.currentPrice.slice(0)));
    },

    coinOptionMenuOpen() {
      this.$store.commit("coinOptionMenuSet", true);
      this.$store.commit("coinOptionSelectedSet", this.coinName);
    },

    colorizer(item: string) {
      var num = Number(item.substring(0, item.length - 1));
      if (num == 0) {
        return "color: #FAA613";
      }
      if (num > 0) {
        return "color: #688E26";
      }
      if (num < 0) {
        return "color: #FAB3A9";
      }
    },

    created() {
      //this.testArr = this.marketDat;
    }
  }
});
